body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    display: grid;
    grid-template-columns: 100vw;
    grid-template-rows: 50vh minmax(20vh, auto) 20vh;
    grid-template-areas: "header" "content" "footer";
}

.app header {
    grid-area: header;
}

.main {
    grid-area: content;
}

.app footer {
    grid-area: footer;
}
.header-back {
    width: 100%;
    height: 151px;
    padding: 44px 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    position: relative;
}

.logo-img {
  width: 20%;
}

.logo-img img {
  width: 300px;
}

.header-links {
  width: 80%;
}

ul {
  margin-left: 0px;
  list-style: none;
  display: flex;
}

li {
  margin: 10px;
  padding: 48px;
  font-family: "Poppins", sans-serif;
  font-size: 19px;
  color: #f04e23;
}

