img {
    border-radius: 100%;
    width: 90%;
    align-items: center;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: ZoomIn;
            animation-name: ZoomIn;
}

@-webkit-keyframes ZoomIn {
    from  {
        -webkit-transform: scale(0.2);
                transform: scale(0.2);
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes ZoomIn {
    from  {
        -webkit-transform: scale(0.2);
                transform: scale(0.2);
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

