.app {
    display: grid;
    grid-template-columns: 100vw;
    grid-template-rows: 50vh minmax(20vh, auto) 20vh;
    grid-template-areas: "header" "content" "footer";
}

.app header {
    grid-area: header;
}

.main {
    grid-area: content;
}

.app footer {
    grid-area: footer;
}